import s from './Header.module.scss';
import Nav from '../Nav/Nav';

const Header = (props) => {
  const handleLoginBtnClicked = () => {
    props.onToggleRegisterVisible();
  };

  return (
    <header className={`${s['Header']}`}>

    {/* Top */}

      <div className={s['top']}>

        <div className={s['logo']}>
          B2Bazarr
        </div>

        <div className={s['top-right']}>
          <div className={s['products-btn']}>
            <span className={s['products-btn-text']}>Продукты</span>
            <i className={`fas fa-sort-down ${s['products-btn-icon']}`}></i>
          </div>

          <div className={s['search-panel']}>
            <input
              className={s['search-input']}
              placeholder="Поиск"
            />
            <button
              className={s['search-btn']}
            >
              Поиск
            </button>
          </div>

          <div className={s['usd-btn']}>
            USD $
            <i className={`fas fa-sort-down ${s['usd-btn-icon']}`}></i>
          </div>

          <div className={s['lang-btn']}>
            <span className={s['lang-btn-text']}>
              Русский
            </span>
            <div className={`fas fa-sort-down ${s['lang-btn-icon']}`}></div>
          </div>
        </div>

      </div>

    {/* Top */}

    {/* Bottom */}

      <div className={s['bottom']}>

        <div className={s['bottom-left']}>
          <button className={s['categories-btn']}>
            Все катерогии
          </button>
          <Nav />
          <button
            className={s['login-btn']}
            onClick={handleLoginBtnClicked}
          >
            <i className={`far fa-user ${s['login-btn-icon']}`}></i>
            Войти
          </button>
        </div>

        <div className={s['bottom-right']}>

          <div className={s['messages-btn']}>
            <i className={`far fa-bell ${s['bottom-right-btn-icon']}`}></i>
            <div className={s['btn-top-right-circle']}>2</div>
          </div>

          <div className={s['like-btn']}>
            <i className={`far fa-heart ${s['bottom-right-btn-icon']}`}></i>
            <div className={s['btn-top-right-circle']}>4</div>
          </div>

          <div className={s['korzina-btn']}>
            <i className={`fas fa-shopping-cart ${s['bottom-right-btn-icon']}`}></i>
            <div className={s['btn-top-right-circle']}>1</div>
            <div className={s['korzina-btn-info']}>
              <span>Корзина</span>
              <span>0.00</span>
            </div>
          </div> 

        </div>

      </div>

    {/* Bottom */}


    </header>
  )
}

export default Header
