import { useState } from 'react';
import s from './Register.module.scss';

const Register = (props) => {
  const [tab, setTab] = useState('register');

  const handleChangeTab = (to) => {
    setTab(to);
  };

  const handleHideRegister = () => {
    props.onToggleRegisterVisible();
  };

  return (
    <div
      className={`${s['shaded-wrapper']} ${s[!props.visible ? 'hidden' : '']}`}
      onClick={handleHideRegister}
    >
      <div
        className={s['Register']}
        onClick={(e) => e.stopPropagation()}
      >

        <div className={s['header']}>
          B2bazarr
        </div>

        <div className={s['body']}>

          <div className={s['buttons']}>
            <button
              className={`${s['login-btn']} ${s[tab === 'login' ? 'selected' : '']}`}
              onClick={() => handleChangeTab('login')}
            >
              Вход
            </button>
            <button
              className={`${s['register-btn']} ${s[tab === 'register' ? 'selected' : '']}`}
              onClick={() => handleChangeTab('register')}
            >
              Регистрация
            </button>
          </div>

          <form
            className={s['form']}
          >

            <div className={s['form-group']}>
              <label htmlFor="email-field" className={s['form-label']}>
                Email
              </label>
              <input
                id="email-field"
                className={`${s['email-field']}`}
              />
            </div>

            <div className={s['form-group']}>
              <label htmlFor="password-field" className={s['form-label']}>
                Пароль
              </label>
              <input
                id="password-field"
                className={s['password-field']}
              />
            </div>

            <input
              type="submit"
              value={tab === 'login' ? 'Войти' : 'Зарегестрироваться'}
              className={s['submit-btn']}
            />

          </form>

        </div>

      </div>
    </div>
  )
}

export default Register
