import s from './Nav.module.scss';
import NavLink from '../NavLink/NavLink';

const Nav = () => {
  return (
    <nav className={s['Nav']}>
      <ul className={s['nav-list']}>
        <NavLink to="/for-sellers">
          Продавцам
        </NavLink>
        <NavLink to="/for-customers">
          Покупателям
        </NavLink>
        <NavLink to="/about-us">
          О нас
        </NavLink>
        <NavLink to="/most-asked-ques">
          Часто задаваемые вопросы
        </NavLink>
        <NavLink to="/contacts">
          Контакты
        </NavLink>
      </ul>
    </nav>
  )
}

export default Nav
